.alarm_item {
  position: relative;
  margin-bottom: 5px;
  padding: 20px;
  box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12), 0 1px 5px 0 rgba(0,0,0,0.2);
  border-radius: 5px;
}

.alarm_top {
  position: relative;
  display: flex;
}

.alarm_menu_button {
  margin-left: auto;
  padding: 10px;
  background-color: transparent;
  border: none;
  transition: all .2s ease-in;
  outline: none;
  cursor: pointer;
}

.alarm_menu_icon {
  font-size: 20px;
}

.alarm_menu {
  position: absolute;
  top: 20px;
  right: 12px;
  z-index: 1;
  margin: 0;
  padding: 20px;
  box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12), 0 1px 5px 0 rgba(0,0,0,0.2);
  border-radius: 5px;
  background-color: #fff;
  list-style: none;
}

.alarm_middle {
  display: flex;
  align-items: center;
}

.alarm_label {
  font-size: 20px;
}

.alarm_switch {
  margin-left: auto;
}

.alarm_switch label {
  cursor: pointer;
}

.delete_icon {
  margin-left: 10px;
  cursor: pointer;
  opacity: .5;
  transition: all .2s ease-in;
}

.delete_icon:hover {
  opacity: 1;
}